<template>
    <div id="liste-joueur" class="container mb-5">
        <div class="row d-flex justify-content-around">
            <div v-for="joueur in joueurFiltre" :key="joueur.id" class="joueur col-11 col-md-5 col-lg-3 col-xl-2 col-xxl-2 pt-3 mx-3 my-3 ">
                <router-link :to="{name: 'FicheJoueur', params: {id: joueur.id}}" class="text-decoration-none">
                    <img class="img-fluid" alt="joueur1" :src="joueur.photo" />
                    <h2 class="text-white">{{joueur.prenom}} {{joueur.nom}}</h2> 
                </router-link>

            </div>



        </div>
    </div>


</template>

<script>

export default {
  name: 'joueursListe',
  data() {
      return {
          listeJoueur : this.$store.state.listeJoueur
      }
  },
  computed : {
      joueurFiltre() {
        return this.listeJoueur.filter((joueur) => {
            return joueur.typeEquipe == this.$route.params.typeEquipe;
        });
        
      }
  }

}
</script>

<style lang="css">
    .joueur {
        background-color: var(--gris-clair-opaque);
        border-radius: 15px;
    }

</style>