import { createRouter, createWebHistory } from 'vue-router'
import MainHome from '../components/MainHome.vue'
import PresentationEquipe from '../components/PresentationEquipe.vue'
import FicheJoueur from '../components/FicheJoueur.vue'
import MainShop from '../components/MainShop.vue'
import MainCompetition from '../components/MainCompetition.vue'
import MainCompte from '../components/MainCompte.vue'


const routes = [
  {
    path: '/',
    name: 'MainHome',
    component: MainHome
  },
  {
    path: '/presentationEquipe/:typeEquipe',
    name: 'PresentationEquipe',
    component: PresentationEquipe
  },
  {
    path: '/ficheJoueur/:id',
    name: 'FicheJoueur',
    component: FicheJoueur
  },
  {
    path: '/shop',
    name: 'MainShop',
    component: MainShop
  },
  {
    path: '/competition',
    name: 'MainCompetition',
    component: MainCompetition
  },
  {
    path: '/compte',
    name: 'MainCompte',
    component: MainCompte
  }   
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
