<template>
    <optionnel-header />
    
    <div id="fiche-joueur" class="container mb-4">
        <h1 class="mb-3">{{ficheFiltre[0].prenom}} {{ficheFiltre[0].nom}}</h1>
        <div class="row d-flex justify-content-evenly">
            <div class="row justify-content-around mb-3">
                <div class="col-12 col-md-6">
                    <img :src="ficheFiltre[0].photo" alt="Photo de profil">
                </div>
                
                <div class="col-12 col-md-6 d-flex flex-column justify-content-center">
                    <h3>Pseudo : {{ ficheFiltre[0].pseudo}}</h3>
                    <h5>Pays : {{ficheFiltre[0].pays}}</h5>
                    <h5>Age : {{ficheFiltre[0].age}} ans</h5>
                    <h5>Equipe : {{ficheFiltre[0].nomEquipe}}</h5>
                </div>
            </div>

            <div class="row">
                <p>{{ficheFiltre[0].description}}</p>
            </div>

            
        </div>
    </div>

    <liste-pages />

  
</template>

<script>
import listePages from'./listePages.vue'
import OptionnelHeader from './OptionnelHeader.vue'

export default {
    name: "FicheJoueur",
    components: {
        listePages,
        OptionnelHeader
    },
    data() {
        return {
            ficheJoueur : this.$store.state.listeJoueur
        }
    },
    computed : {
      ficheFiltre() {
          return this.ficheJoueur.filter((joueur) => {
                return joueur.id == this.$route.params.id;
            });
      }
  }

}
</script>

