<template>
    <optionnal-header />

    <div id="shop" class="container">
        <h1>Shop</h1>
        <div class="row">
            <div class="col-12 mx-3 my-3 d-flex ">
                <input v-model="searchKey" id="recherche" type="search" placeholder="Rechercher..." autocomplete="off">
                <span v-if="searchKey && filteredProducts.length >= 1 ">
                    {{filteredProducts.length}} résultat<span v-if="filteredProducts.length >= 2">s</span>
                </span>
            </div>
            <div class="col-12 col-lg-7 flex-grow-1">
                <div class="row justify-content-evenly">
                    <div v-for="product in filteredProducts" :key="product.id" class="produit-shop col-11 col-md-5 col-lg-3 pt-3 mx-3 my-3 flex-wrap">
                        <div class="img-produit">
                            <img :src="product.img" class="img-fluid"/>
                        </div>

                        <div class="descriptif-produit d-flex justify-content-between align-items-center">
                            <h5>{{ product.titre }}</h5>
                            <span>{{ product.prix }}€ ou {{ product.prix / 0.01 }} </span>
                        </div>

                        <div class="d-flex justify-content-evenly align-items-center">
                            <label v-if="articleFavoris" v-bind:for="product.id">
                                <i class="fas fa-heart"></i>
                            </label>
                            <i @click="commander(product)" class="fas fa-shopping-cart py-2 px-2"></i>
                        </div>
                    

                    </div>
                </div>

                <div v-if="filteredProducts.length == []">
                    <h1>Oups...</h1>
                    <h2>Désolé, aucun produit ne correspond à votre recherche</h2>
                </div>
            </div>

            <div v-if="cart.length > 0" class="col-12 col-lg-5">
                <h2>Votre panier</h2>
                <div v-for="product, id in cart" :key="product.id" class="row produit-shop col-12 d-flex align-items-center py-2 my-2">
                    <img :src="product.img" class="col-3 img-fluid">
                    <h5 class="col-3 mb-0 ">{{ product.titre }}</h5>
                    <div class="col-1 row mb-0">
                        <i class="fas px-0 fa-regular fa-plus" @click="addQuantityOfProduct(product)"></i>
                        <h6 class="px-0 my-0">{{ product.quantite }}</h6>
                        <i class="fas px-0 fa-regular fa-minus" @click="removeQuantityOfProduct(product, id)"></i>
                    </div>
                    <!-- <h6 class="col-1 mb-0 ">{{ product.quantity }}</h6> -->
                    <h6 class="col-5 mb-0 ">{{ product.prix * product.quantite }}€ ou {{ (product.prix * product.quantite)/0.01 }} <img src="../../public/images/logo-veron.png" alt="" class="img-fluid img-icone"></h6>
                    
                </div>
                <h4 class="my-2">{{nombreTotalArticle}} article<span v-if="nombreTotalArticle>1">s</span> pour {{coutPanierTotal}}€ ou {{coutPanierTotal/0.01}} <img src="../../public/images/logo-veron.png" alt="" class="img-fluid img-icone"></h4>
                <button @click="debuterPaiement"><h5 class="px-3 py-1 mb-0">Payer</h5></button> 

                <div v-if="debutPaiement" class="col-12 d-flex my-3">
                    <div class="col-6 row d-flex">
                        <div class="col-12 d-flex align-items-center">
                            <input type="checkbox">
                            <label>Icones</label>
                        </div>
                        <div class="col-12 d-flex align-items-center">
                            <input type="checkbox">
                            <label>Carte Bancaire</label>
                        </div>
                    </div>
                    <div class="col-6">
                        <h5 v-if="paimentEffectue" class="px-3 py-1 mb-0">Paiement effectué</h5>
                        <button v-else @click="effectuerPaiement"><h5 class="px-3 py-1 mb-0">Procéder au paiement</h5></button>
                    </div>
                    
                </div>
            </div>

            
        </div>
    </div>

  
</template>

<script>
import OptionnalHeader from './OptionnelHeader.vue'

export default {
  components: { OptionnalHeader },
    name: "MainShop",
    data() {
        return {
            debutCommande: false,
            products: this.$store.state.articleStock,
            cart: [],
            searchKey: '',
            articleFavoris: false,
            debutPaiement: false,
            paimentEffectue: false
        }
    },
    methods: {
        commander(produitAjoute) {
            for (let i = 0; i < this.cart.length; i++) {
                if (this.cart[i].id === produitAjoute.id) {
                    return this.cart[i].quantite++ 
                }
            } 
            this.cart.push({
                id: produitAjoute.id,
                titre: produitAjoute.titre,
                description: produitAjoute.description,
                prix: produitAjoute.prix,
                img: produitAjoute.img,
                quantite: 1
            })
        },
        addQuantityOfProduct(product) {
            product.quantite ++;
        },
        removeQuantityOfProduct(product, id) {
            if (product.quantite == 1) {
                this.cart.splice(id,1)
            } else {
                product.quantite --;
            }
        },
        debuterPaiement() {
            this.debutPaiement = true
        },
        effectuerPaiement() {
            this.paimentEffectue = true
            this.cart = []
        }
    },
    computed: {
        filteredProducts() {
            return this.products.filter((product) => {
                return product.titre.toLowerCase().includes(this.searchKey.toLowerCase());
            });
        },
        coutPanierTotal() {     
            let coutPanier = 0;
            for (let id in this.cart) {
                coutPanier += this.cart[id].quantite * this.cart[id].prix;
            }
            return coutPanier;
        },
        nombreTotalArticle() {
            let nombreArticle = 0;
            for (let id in this.cart) {
                nombreArticle += this.cart[id].quantite ;
            }
            return nombreArticle;
        }
    }
}

</script>



<style>
.produit-shop {
    background-color: var(--gris-clair-opaque) ;
    border-radius: 15px;
    color: white;
}

</style>